

document.addEventListener('DOMContentLoaded', function() {

    let form = document.querySelector('.calc')

    // валидация
    Pristine.addValidator("my-range", function(value, param1, param2) {
        return parseInt(param1) <= value && value <= parseInt(param2) 
    }, "Значение должно быть не больше ${2}", 5, false);


    Pristine.addValidator("my-range2", function(value) {
        return value.indexOf('_') == -1
    }, "Телефон указан неверно", 5, false);

    var pristine = new Pristine(form,{
        classTo: 'form-group',
        errorClass: 'error',
        successClass: 'has-success',
        // class of the parent element where error text element is appended
        errorTextParent: 'form-group',
        // type of element to create for the error text
        errorTextTag: 'div',
        // class of the error text element
        errorTextClass: 'error__text' 
    });
    
    form.addEventListener('submit', function (e) {
        e.preventDefault();
        
        // check if the form is valid
        var valid = pristine.validate(); // returns true or false
        if(valid){
            form.classList.add('active')
        }
    });


    Inputmask({"mask": "+7 (999) 999-99-99"}).mask(document.querySelectorAll(".phone"));


    // Калькулятор Своё дело. КАСКО
    if(document.getElementById('form1')){

        // Поля формы
        let form1__typeInput = document.getElementById('form1__typeInput').querySelectorAll('input')
        let form1__type = document.querySelectorAll('.form1__type')
        let form1__type__arr = [
            [1.8, 2.5, 1, 4],
            [1.08, 1.5, 0.6, 2.4],
        ]
        let total = document.getElementById('total')
        let totalInput = document.getElementById('total-input') 
        let priceInput = document.getElementById('price')

        // подписка на события
        priceInput.addEventListener('input', ()=>{
            formCounter();
        })

        form.querySelectorAll('input').forEach(el=> el.addEventListener('change', ()=>{
            formCounter();
        }))

        form.querySelectorAll('input').forEach(el=> el.addEventListener('input', ()=>{
            formCounter();
        }))

        form1__type.forEach(el=> el.addEventListener('change', ()=>{
            refreshCoef(el)
        }))

        // Изменить кофициенты
        const refreshCoef = (el)=>{
            let val = el.getAttribute('value')

            form1__typeInput.forEach((input, index)=>{
                input.setAttribute('value', form1__type__arr[val][index])
            })

            formCounter();
        }
        
        // Расчет премии
        const formCounter = () =>{
            let coef = document.getElementById('form1__typeInput').querySelector('input:checked').value
            let price = document.getElementById('price').value

            let totalPrice = coef / 100 * price
            totalPrice = totalPrice.toFixed(2)

            total.textContent = totalPrice;
            totalInput.setAttribute('value', totalPrice)
        }

        formCounter();

    }

    // Калькулятор Своё дело.
    // Спецтехника
    if(document.getElementById('form2')){
        let total = document.getElementById('total')
        let totalInput = document.getElementById('total-input') 

        form.querySelectorAll('input').forEach(el=> el.addEventListener('change', ()=>{
            formCounter();
        }))

        form.querySelectorAll('input').forEach(el=> el.addEventListener('input', ()=>{
            formCounter();
        }))


        const formCounter = () =>{
            let price = document.getElementById('price').value
            let coef = 0.13;
            let count = document.querySelectorAll('.form2__row').length

            let totalPrice = price * coef * count;
            totalPrice =  totalPrice.toFixed(2)

            total.textContent = totalPrice;
            totalInput.setAttribute('value', totalPrice)
        }

        formCounter();

        let addTech = document.getElementById('add-tech')
        let htmlBlock = document.querySelector('.form2__row');

        document.addEventListener('click', (e)=>{
            
            if(e.target.closest('.delete')){
                e.target.closest('.form2__row').remove()

                addTech.closest('.calc-add').classList.remove('max')
                formCounter();
            }
        })

        addTech.addEventListener('click', ()=>{
            addTechF()
        }) 

        const addTechF = ()=>{
            let count = document.querySelectorAll('.form2__row').length
            let htmlBlockClone = htmlBlock.cloneNode(true)
            
            if(count <= 4){
                document.querySelector('.form2__row-wrap').appendChild(htmlBlockClone)
                document.querySelector('.form2__row-wrap').classList.add('active')
                if(count == 4){
                    addTech.closest('.calc-add').classList.add('max')
                }

                formCounter();

                pristine = new Pristine(form,{
                    classTo: 'form-group',
                    errorClass: 'error',
                    successClass: 'has-success',
                    // class of the parent element where error text element is appended
                    errorTextParent: 'form-group',
                    // type of element to create for the error text
                    errorTextTag: 'div',
                    // class of the error text element
                    errorTextClass: 'error__text' 
                });
            }
        }
    }

    // Калькулятор Своё дело.
    // Грузоперевозки
    if(document.getElementById('form3')){

        let total = document.getElementById('total')
        let totalInput = document.getElementById('total-input') 

        let form3Variats = document.getElementById('form3Variat').querySelectorAll('input')
        
        let form3__type__arr = [
            [0.0012, 0.0009, 0.0005, 0.0007],
            [0.002, 0.0017, 0.0013, 0.0015],
        ]
        let form3__typeInputs = document.getElementById('form3__typeInputs').querySelectorAll('input')


        form3Variats.forEach(el=> el.addEventListener('change', ()=>{
            refreshCoef3(el)
        }))

        // Изменить кофициенты
        const refreshCoef3 = (el)=>{
            let val = el.getAttribute('value')

            form3__typeInputs.forEach((input, index)=>{
                input.setAttribute('value', form3__type__arr[val][index])
            })

            formCounter();
        }


        const formCounter = () =>{
            let price = document.getElementById('price').value
            
            let coef110 = 1; 
            if(document.getElementById('varian110').checked){
                coef110 = 1.1
            }

            let coef = document.getElementById('form3__typeInputs').querySelector('input:checked').value

            
            let totalPrice = price * coef110 * coef;
            totalPrice = totalPrice.toFixed(2)

            total.textContent = totalPrice;
            totalInput.setAttribute('value', totalPrice)
        }

        formCounter();


        form.querySelectorAll('input').forEach(el=> el.addEventListener('change', ()=>{
            formCounter();
        }))

        form.querySelectorAll('input').forEach(el=> el.addEventListener('input', ()=>{
            formCounter();
        }))
    }

})
